.BackDrop {
  position: relative;
}
.BackDrop .BackDrop-content {
  position: relative;
  transition: 0.1s all cubic-bezier(0.24, 0.94, 1, 1);
}
.BackDrop--show {
  z-index: 99999;
}
.BackDrop--show .BackDrop-content {
  box-shadow: 0 0 0 9999px rgba(0,0,0,0.8);
}
.scroll-fix {
  overflow: hidden;
}
