.Btn {
  position: relative;
  padding: 8px 20px;
  line-height: 16px;
  color: #1d3250;
  border-radius: 2px;
  font-size: 16px;
  border: none;
  outline: none;
  transition: 0.15s;
  transform-origin: center;
  cursor: pointer;
}
.Btn:disabled {
  cursor: not-allowed !important;
  opacity: 0.5;
}
.Btn.Btn--link {
  padding: 8px 0;
  background-color: transparent;
  box-shadow: none;
  color: #1d3250;
}
.Btn.Btn--link:not(:disabled):hover {
  background-color: inherit;
  text-decoration: underline;
}
.Btn.Btn--link:not(:disabled):active {
  background-color: inherit;
  text-decoration: underline;
}
.Btn--AsyncBtn .Btn-text,
.Btn--AsyncBtn .Spinner {
  vertical-align: middle;
}
.Btn--AsyncBtn .Btn-text {
  display: inline-block;
}
.Btn--AsyncBtn .Spinner {
  position: absolute;
  display: none;
}
.Btn--AsyncBtn .Btn-text {
  transition: 0.2s padding cubic-bezier(0.2, 0.9, 1, 1);
}
.Btn--AsyncBtn.Btn--AsyncBtn--withSpinner .Spinner {
  left: 16px;
}
.Btn--AsyncBtn.Btn--AsyncBtn--withSpinner span + .Spinner {
  left: unset;
  right: 16px;
}
.Btn--AsyncBtn.Btn--AsyncBtn--isPending {
  height: 34px;
}
.Btn--AsyncBtn.Btn--AsyncBtn--isPending .Spinner {
  display: inline-block;
}
.Btn--AsyncBtn.Btn--AsyncBtn--isPending.Btn--AsyncBtn--withSpinner .Btn-text {
  padding: 0 28px 0 0;
}
.Btn--AsyncBtn.Btn--AsyncBtn--isPending.Btn--AsyncBtn--withSpinner .Spinner + .Btn-text {
  padding: 0 0 0 28px;
}
