.ToastsContainer {
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 100000;
}
.ToastsContainer > div:not(:first-of-type) {
  margin-top: 16px;
}
.Toast {
  position: relative;
  width: 480px;
  padding: 12px;
  border-radius: 3px;
  text-align: center;
  background: #fff;
  color: #1d3250;
}
.Toast.Toast--hasCross {
  padding-right: 32px;
}
.Toast .Toast-content {
  line-height: 20px;
}
.Toast .Toast-cross {
  position: absolute;
  right: 6px;
  top: 10px;
  padding: 0 4px;
  font-size: 20px;
  cursor: pointer;
}
