.SmartTipContent {
  position: absolute;
}
.SmartTipContent::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%);
  width: 30px;
  height: 12px;
  background-color: inherit;
}
.SmartTipContent .arrow {
  position: absolute;
  bottom: -4px;
  width: 12px;
  height: 12px;
  left: 50%;
  transform: translate(-50%) rotate(48deg) skew(8deg);
  border-radius: 2px;
  background-color: inherit;
  box-shadow: inherit;
  transform-origin: center;
}
